* {
  list-style: none;
  }
body {
  background-color: #ccc;
}
.width-90{
  width: 90%;
  margin: auto;
  padding: 10px;
}
.App {
  text-align: center;
}

.app-header {
  background-color: #282c34;
  min-height: 10vh;
  font-size: calc(10px + 2vmin);
  color: white;
}
.header-title {
  padding: 0 !important;
}
input,select {
  width: 80%;
  min-width: 150px;
  margin-bottom: 10px;
}
.login-input {
  max-width: 350px;
  margin: 0 auto;
  display: block;
}
.login-checkbox {
  display: block;
  width: 10px;
  margin: 10px auto;
}
label {
  display: block;
}
.row-header {
  background-color: #999;
  margin: 0 auto;
  width: 100%;
  border: 1px #333 solid;
  padding: 10px 0;
}
.li {
  margin: auto;
  width: 100%;
  padding: 10px 0;
}
.li:hover {
  background-color: #a3d5f0;
  cursor: pointer;
}
.li-active {
  background-color: #a3d5a0;
  cursor: pointer;
}
button {
  margin: 1px;
}
.hidden-input {
  position: absolute;
  top: -200px;
}
.margin-top {
  margin-top: 25px;
}
.margin-bottom {
  margin-bottom: 25px;
}
.modal-view {
  position: fixed;
  top:0;
  left:0;
  background-color: rgba(0,0,0,0.6);
  width: 100%;
  height: 100%;
  z-index: 5;
}
.modal-box {
  position: fixed;
  background-color: #fff;
  box-shadow: 0px 10x 15px 2px #666;
  padding: 30px;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  width: 60%;
  min-width: 300px;
  z-index: 6;

}